<template>
	<div id="member-setting">
		<public-header title="我的设置"></public-header>
		<div class="content">
			<van-cell title="修改密码" class="margin-15-t" to="/pages/member/profilePassword">
				<van-icon slot="right-icon" name="arrow" />
			</van-cell>
			<div class="padding-15" @click="onLoginOut">
				<van-button size="normal" block class="bg-primary font-16">退出登录</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {

			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoginOut() {
				var that = this;
				that.$dialog.confirm({
					title: '温馨提示',
					message: '确定退出登录吗？'
				}).then(() => {
					this.util.removeStorage('deliveryerInfo');
					this.util.removeStorage('idtoken');
					if(this.util.isDlala()) {
						api.removePrefs({
							key: 'idtoken'
						});
					}
					this.$router.replace(this.util.getUrl({path: '/pages/auth/login'}));
					return;
				})
			}
		}
	}
</script>

<style>

</style>


